.food {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 1120px;

  @include trebuchet;
}

.food__card-wrapper {
  overflow: hidden;
  z-index: 0;
  position: relative;
  box-sizing: border-box;
  width: 320px;
  height: 480px;
  padding: 4px;
  border-radius: 12px;

  &::before {
    position: absolute;
    top: -55px;
    left: -55px;
    z-index: -1;
    padding: 45px;
    transform: rotate(45deg);
    box-shadow: 0 0 0 600px $blue;
    content: "";
  }
}

.food__card {
  overflow: hidden;
  z-index: 0;
  position: relative;
  display: flex;
  flex-direction: column;

  box-sizing: border-box;
  width: 312px;
  height: 472px;
  padding: 17px 11px 11px 45px;

  color: $grey-text;

  // clip-path: polygon(43px 0, 100% 0%, 100% 100%, 0% 100%, 0 43px);

  background: url("../img/cat@1x.png") top 203px left -28px no-repeat;
  border-radius: 9px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background: url("../img/cat@2x.png") top 203px left -28px / 370px 362px no-repeat;
  }

  &::before {
    position: absolute;
    top: -56px;
    left: -56px;
    z-index: -1;
    padding: 45px;
    transform: rotate(45deg);
    box-shadow: 0 0 0 600px $white;
    content: "";
  }
}

.food__tagline {
  font-size: 16px;
  line-height: 19px;
}

.food__name {
  margin: 5px 0 15px;
  text-indent: -2px;
  font-size: 48px;
  line-height: 56px;
  color: $black;
}

.food__taste {
  display: block;
  text-indent: 0;
  font-size: 24px;
  line-height: 26px;
}

.food__advantages-list {
  @include reset-list;
}

.food__advantages-item {
  font-size: 14px;
}

.food__amount {
  align-self: flex-end;

  box-sizing: border-box;
  width: 80px;
  height: 80px;
  margin: 0;
  margin-top: auto;
  padding-top: 10px;

  font-size: 42px;
  line-height: 42px;
  text-align: center;
  color: $white;

  border-radius: 50%;
  background-color: $blue;
}

.food__measure {
  display: block;
  font-size: 21px;
  line-height: 18px;
}

.food__descriptor {
  margin: 15px 0 24px;
  font-size: 13px;
  text-align: center;
}

.food__link {
  color: $blue;
  text-decoration: none;
  border-bottom: 1px dashed $blue;
}

@media (max-width: $tablet-width) {
  .food {
    width: 720px;
  }

  .food__item {
    margin-right: 80px;

    &:nth-child(2n) {
      margin-right: 0;
    }

    &:last-child {
      margin: auto;
    }
  }
}

@media (max-width: $mobile-width) {
  .food {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .food__item {
    margin-right: 0;
  }
}
