html,
body {
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0; }

.page-main {
  width: 100%;
  height: 100%;
  overflow: auto;
  white-space: nowrap;
  text-align: center;
  font-size: 0;
  color: #ffffff;
  background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 50%, #000000 100%), url("../img/pattern@1x.jpg"), #000000; }
  .page-main::before {
    height: 100%;
    display: inline-block;
    vertical-align: middle;
    content: ""; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-main {
      background: linear-gradient(0deg, #000000 0%, rgba(0, 0, 0, 0) 50%, #000000 100%), url("../img/pattern@2x.jpg") top left/70px 70px, #000000; } }

.page-main__wrapper {
  display: inline-block;
  white-space: normal;
  vertical-align: middle;
  text-align: left; }

.page-main__title {
  margin: 24px 0;
  font-family: "Exo 2.0", "Trebuchet MS", "Helvetica", sans-serif;
  font-size: 36px;
  line-height: 42px;
  font-weight: 100;
  text-align: center; }

.page-main__food-list {
  margin: 0;
  padding: 0;
  list-style: none; }

@media (max-width: 767px) {
  .page-main__title {
    width: 320px; } }

.food {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 1120px;
  font-family: "Trebuchet MS", "Helvetica", sans-serif; }

.food__card-wrapper {
  overflow: hidden;
  z-index: 0;
  position: relative;
  box-sizing: border-box;
  width: 320px;
  height: 480px;
  padding: 4px;
  border-radius: 12px; }
  .food__card-wrapper::before {
    position: absolute;
    top: -55px;
    left: -55px;
    z-index: -1;
    padding: 45px;
    transform: rotate(45deg);
    box-shadow: 0 0 0 600px #1698d9;
    content: ""; }

.food__card {
  overflow: hidden;
  z-index: 0;
  position: relative;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 312px;
  height: 472px;
  padding: 17px 11px 11px 45px;
  color: #666666;
  background: url("../img/cat@1x.png") top 203px left -28px no-repeat;
  border-radius: 9px; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .food__card {
      background: url("../img/cat@2x.png") top 203px left -28px/370px 362px no-repeat; } }
  .food__card::before {
    position: absolute;
    top: -56px;
    left: -56px;
    z-index: -1;
    padding: 45px;
    transform: rotate(45deg);
    box-shadow: 0 0 0 600px #ffffff;
    content: ""; }

.food__tagline {
  font-size: 16px;
  line-height: 19px; }

.food__name {
  margin: 5px 0 15px;
  text-indent: -2px;
  font-size: 48px;
  line-height: 56px;
  color: #000000; }

.food__taste {
  display: block;
  text-indent: 0;
  font-size: 24px;
  line-height: 26px; }

.food__advantages-list {
  margin: 0;
  padding: 0;
  list-style: none; }

.food__advantages-item {
  font-size: 14px; }

.food__amount {
  align-self: flex-end;
  box-sizing: border-box;
  width: 80px;
  height: 80px;
  margin: 0;
  margin-top: auto;
  padding-top: 10px;
  font-size: 42px;
  line-height: 42px;
  text-align: center;
  color: #ffffff;
  border-radius: 50%;
  background-color: #1698d9; }

.food__measure {
  display: block;
  font-size: 21px;
  line-height: 18px; }

.food__descriptor {
  margin: 15px 0 24px;
  font-size: 13px;
  text-align: center; }

.food__link {
  color: #1698d9;
  text-decoration: none;
  border-bottom: 1px dashed #1698d9; }

@media (max-width: 1279px) {
  .food {
    width: 720px; }
  .food__item {
    margin-right: 80px; }
    .food__item:nth-child(2n) {
      margin-right: 0; }
    .food__item:last-child {
      margin: auto; } }

@media (max-width: 767px) {
  .food {
    flex-direction: column;
    align-items: center;
    width: 100%; }
  .food__item {
    margin-right: 0; } }
